import React from 'react'
import styled from 'styled-components'
import Fade from 'react-reveal'

function Section(props) {
    return (
        <Wrap bgImg={props.backgroundImg}>
            <Fade bottom>
            <ItemText>
                <h1>{ props.title }</h1>
                <p>{ props.description }</p>
            </ItemText>
            </Fade>

           <Buttons>
           <Fade bottom>
           <ButtonGroup>
                <LeftButton>
                    { props.leftbtnText }  
                </LeftButton>
                { props.rightbtnText &&     //line 17 - 21 mean if righbtntext exist then show <RightButton></RightButton>
                    <RightButton>  
                    { props.rightbtnText }
                    </RightButton>
                }
            </ButtonGroup>
           </Fade>
            <ArrowDown src="../../Tesla Clone LIVE/images/down-arrow.svg" />
           </Buttons>
        </Wrap>
    )
}

export default Section

const Wrap = styled.div`
    width:100vw;
    height:100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: ${props => `url("../../Tesla Clone LIVE/images/${props.bgImg}")`};
    display: flex;
    flex-direction: column;
    justify-content: space-between; //for vertical alignment
    align-items: center; //for horizontal alignment

`

const ItemText = styled.div`
    padding-top: 15vh;
    text-aling: center;

`

const ButtonGroup = styled.div`
    display: flex;
    margin-bottom: 30px;
    @media (max-width: 575px) {
        flex-direction: column;
    }
`
const LeftButton = styled.div`
    background-color: rgba(23,26,32,0.8);
    height: 40px;
    width: 256px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius:100px;
    opacity:0.85;
    text-transform: uppercase;
    font-size:12px;
    cursor: pointer;
    margin:8px
`
const RightButton = styled(LeftButton)`
    background-color: white;
    opacity: 0.65;
    color: black;
`

const ArrowDown = styled.img`
    height: 40px;
    animation: animateDown infinite 1.5s;
    overflow-x: hidden;
`

const Buttons = styled.div`

`






